.resume {
  padding: 20px 20px 20px 20px;
  margin-right: 20px;
  background-color: linen;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;

  p {
    padding: 10px;
    text-align: center;
    width: 700px;
  }

  h2 {
    text-align: center;
    font-size: 30px;
  }

  h3 {
    text-align: center;
    font-size: 20px;
  }

  ul {
    margin: 10px;
    text-align: center;

    li {
      list-style: none;
    }
  }
}
