@import "../../global.scss";

.topbar {
  width: 100%;
  height: 100px;
  background-color: $mainColor;
  color: white;
  position: fixed;
  top: 0;
  z-index: 2;
  align-items: center;

  @include mobile {
    height: 180px;
  }

  .wrapper {
    padding: 10px 30px;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;

    .left {
      display: flex;
      align-items: center;

      .logo {
        font-size: 40px;
        font-weight: 700;
        text-decoration: none;
        color: inherit;
        margin-right: 40px;
      }

      .itemContainer {
        display: flex;
        align-items: center;
        margin-left: 30px;

        .icon {
          font-size: 18px;
          margin-right: 50px;
          text-decoration: none;
        }
      }
      ul {
        list-style: none;
        list-style-type: none;
        font-size: 20px;

        li {
          display: inline-block;

          a {
            font-size: inherit;
            color: inherit;
            text-decoration: none;
          }
          &:hover {
            color: #ffd3ba;
          }
        }
      }
    }
    .right {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 10px 10px;
      text-decoration: none;
      color: inherit;
      cursor: pointer;

      ul {
        list-style-type: none;
        list-style: none;
        font-size: 20px;
        font-weight: 300;

        li {
          list-style: none;
          display: inline-block;
          padding: 15px;

          a {
            font-size: inherit;
            color: inherit;
            text-decoration: none;
          }

          &:hover {
            font-weight: 400;
          }
        }
      }
    }
  }
}
