.footer {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 10px;
  background-color: #1d848e;

  .icon {
    font-size: 18px;
    text-decoration: none;
    color: white;
  }

  ul {
    list-style: none;
    list-style-type: none;
    font-size: 20px;

    li {
      display: inline-block;

      a {
        font-size: inherit;
        color: inherit;
        text-decoration: none;
      }
      &:hover {
        color: #ffd3ba;
      }
    }
  }
}
